.card-page {
  @include container;
}

.card-page__title {
  max-width: 570px;
  font-family: Times New Roman;
  font-weight: 700;
  font-size: 30px;
  line-height: 40px;
  text-transform: uppercase;
  color: #562F2F;
}

.card-page__list {
  @include list;

  img {
    margin: auto;
    max-height: 500px;
    width: auto !important;
  }

  li {
    border: 2px solid #ffefe9;
  }
}

.card-page__box {
  max-height: 300px;
  overflow: auto;
  margin-bottom: 25px;
}

.card-page__info-text {
  margin: 5px 0;
  font-size: 16px;
  line-height: 22px;
  color: #562F2F;

  span {
    font-weight: 400;
    font-size: 16px;
    color: #7C6C6C;
  }

  &--price {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 25px 10px;
    background-color: #FFEFE9;
    font-weight: 700;
    font-size: 24px;
    line-height: 34px;
    color: #A74242;
    text-align: center;

    span {
      margin-right: 10px;
    }
  }
}

.card-page__buy-btn {
  display: block;
  max-width: 209px;
  margin: auto;
  padding: 19px 15px;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  color: #FFFFFF;
  background-color: #5B1717;
  border-radius: 50px;
  text-decoration: none;
}

.card-page__description {
  color: #6B5151;
  font-size: 16px;

  h2 {
    margin-bottom: 0;
    font-size: 24px;
    line-height: 32px;
    font-weight: 400;
  }

  h1 {
    display: none;
  }

  p, ul {
    font-size: 14px;
  }
}

@media (min-width: 768px) {
  .card-page__info-box {
    display: flex;
  }

  .card-page__list {
    width: 70%;
    padding: 30px;
    padding-top: 0;

    img {
      margin: auto;
      max-height: 500px;
      width: auto !important;
      min-height: 300px;
    }

    .owl-nav {
      left: 30px;
    }

    li {
      display: flex;
      min-height: 500px;
    }
  }

  .card-page__description {
    padding-left: 30px;
    max-width: 700px;
    margin-bottom: 100px;

    h2 {
      margin: 0;
    }
  }

  .card-page__title {
    padding-left: 30px;
  }

  .card-page__info {
    flex-grow: 1;
  }
}
