.sort {
  @include container;
  padding: 15px 0;
}

.sort__list {
  @include list;
  display: flex;
  flex-wrap: wrap;

  &--category {
    display: block;
    max-height: 200px;
    overflow: hidden;
    transition: max-height 0.7s ease;

    .sort__item {
      margin: 0;
    }

    .sort__link {
      padding: 3px 5px 3px 30px;
      font-weight: 600;
      font-size: 14px;
      text-align: left;
      background-image: url("../img/arrow-right.svg");
      background-repeat: no-repeat;
      background-position: left 10px center;

      &:hover {
        background-color: #FBD2A4;
        color: #562F2F;
      }

      &--child {
        margin-left: 15px;
        font-weight: 400;
        background-image: url("../img/ellipse.svg");
      }

      &--parent {
        display: flex;
        justify-content: space-between;
        margin-left: 0;
        font-weight: 600;
        background-image: url("../img/arrow-down.svg");

        span {
          font-weight: 400;
          font-size: 12px;
          padding-left: 20px;
          background-image: url("../img/arrow-left.svg");
          background-repeat: no-repeat;
          background-position: left center;
        }
      }
    }

    &-decor {
      max-height: none;
    }
  }

  &--show-all {
    max-height: 500px;
  }
}

.sort__item {
  margin: 5px;
  flex-grow: 1;
}

.sort__link {
  display: block;

  padding: 15px 13px;
  color: #70635D;
  font-size: 16px;
  line-height: 22px;
  font-weight: 700;
  background-color: #FFEFE9;
  text-decoration: none;
  text-align: center;
  transition: 0.3s ease;

  &:hover,
  &--active {
    color: #fff;
    background-color: #391111;
  }

  &--hide {
    display: none;
  }
}

.filter__category-view {
  color: #5B1717;
  font-size: 12px;
  font-weight: 400;

}

.sort__category-view {
  color: #5B1717;
  font-size: 12px;
  padding: 0 20px 0 0;
  margin-left: 20px;
  text-decoration: none;
  background-image: url("../img/arrow-down.svg");
  background-repeat: no-repeat;
  background-position: right center;
  border-bottom: 1px dashed;

  &--open {
    background-image: url("../img/arrow-up.svg");
  }
}

@media (min-width: 1024px) {
  .sort {
    padding: 15px;
  }
}
